
.footer-content {
    position: fixed;
    padding: 20px;
    bottom: 0;
    width: 100%; 
    background-color: darkgrey;
    color: white;
    text-align: center;
}
/* Self portrait formatting */
.headshot{    
    border-style: solid;
    border-width: 2px;
    border-radius: 100%;
    border-color: var(--bgHeader);
    max-width: 90%;
    max-height: auto;
    box-shadow: 3px 3px 5px rgb(160,160,160);
}

p {
    padding: 5px;
}

.about-margin {
    margin-top:100px;
}
.project-margin {
    margin-top: 40px;
}
.contact-margin {
    margin-top: 40px;
    margin-bottom: 100px;
}
